import React, { useEffect, useState } from "react";
import {
  Link,
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useUtilContext } from "hooks/UtilContext";

import "pages/Event/Customer.css";

const Event = () => {
  useEffect(() => {
    setSearch(""); //     
  }, []); //         

  const {
    isActive,
    selectedCategory,
    currentPage,
    setCurrentPage,
    toggleActive,
    handleCategoryClick,
    handlePageChange,
    search,
    setSearch,
    eventPages,
    eventitemsPerPage,
    currentBlock,
    maxVisiblePages,
    startPage,
    activeTab,
    setActiveTab,
    today,
    datefilteredEvents,
    totalFilteredPages,
  } = useUtilContext();

  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (location.search.includes("cate=")) {
      navigate("/event", { replace: true }); // URL   
    }
  }, []);

  const handleCategoryChange = (e) => {
    handleCategoryClick(selectedCategory);
    navigate(`/event?cate=${e}`);
  };

  const [filteredEvents, setFilteredEvents] = useState(datefilteredEvents);

  const searchAction = (e) => {
    e.target.closest(".search_event").classList.toggle("active");
  };

  useEffect(() => {
    if (search.trim() === "") {
      setFilteredEvents(datefilteredEvents);
    } else {
      const filtered = datefilteredEvents.filter(
        (event) =>
          event.title.toLowerCase().includes(search.toLowerCase()) ||
          event.title2.toLowerCase().includes(search.toLowerCase())
      );
      setFilteredEvents(filtered);
    }
    // setCurrentPage(1); //     => pagination  
  }, [search, selectedCategory, datefilteredEvents, setCurrentPage]);

  //    
  const isSearching = search.trim() !== ""; // 
  const displayedEvents = isSearching ? filteredEvents : datefilteredEvents; //    
  const paginatedEvents = displayedEvents.slice(
    (currentPage - 1) * eventitemsPerPage,
    currentPage * eventitemsPerPage
  );
  const eventSearchTotalPages = Math.ceil(
    filteredEvents.length / eventitemsPerPage
  );
  const eventSearchEndPage = Math.min(
    currentBlock * maxVisiblePages,
    eventSearchTotalPages
  );
  const searchPages = Array.from(
    { length: eventSearchEndPage - startPage + 1 },
    (_, i) => startPage + i
  );
  const eventPage = isSearching ? searchPages : eventPages;

  // 
  // const [searchParams] = useSearchParams();
  // const urlCate =  searchParams.get("cate");

  return (
    <div id="container" className="board__event">
      <div className="layout_fix">
        <div className="heading">
          <h2 className="tit"></h2>
          <ul className="sort_list">
            <li className="sort_item">
              <label></label>
              <Link
                to={`/event?cate=${selectedCategory}`}
                className={`${isActive ? "active" : ""}`}
                onClick={toggleActive}
              >
                {selectedCategory}
              </Link>
              <ul className="dropdown">
                {["", "", ""].map((category) => (
                  <li key={category}>
                    <Link
                      to={{
                        pathname: `/event/${datefilteredEvents.idx}`,
                        search: `?cate=${category}`,
                      }}
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleCategoryChange(category);
                        handleCategoryClick(category);
                      }}
                    >
                      {category}
                    </Link>
                  </li>
                ))}
              </ul>
            </li>
          </ul>
        </div>

        <div className="heading_tab">
          <div className="tab_rect">
            {/*     */}
            <p
              className={`event_ing ${activeTab === "ongoing" ? "active" : ""}`}
              onClick={() => {
                setActiveTab("ongoing");
                setCurrentPage(1); //     1 
              }}
            >
               
            </p>

            {/*    */}
            <p
              className={`event_end ${activeTab === "past" ? "active" : ""}`}
              onClick={() => {
                setActiveTab("past");
                setCurrentPage(1); //     1 
              }}
            >
               
            </p>
          </div>
          <div className="search_event">
            <input
              value={search}
              onChange={(e) => {
                setSearch(e.target.value);
              }}
              type="text"
              placeholder=" "
            />
            <button className="btn_search" onClick={(e) => searchAction(e)}>
              Search
            </button>
          </div>
        </div>

        <ul className="event_list">
          {paginatedEvents.length > 0 ? (
            paginatedEvents.map((event, idx) => {
              const isEnded = event.endDate && new Date(event.endDate) < today; //  
              return (
                <li key={idx} className={isEnded ? "li-ended" : ""}>
                  <Link
                    to={{
                      pathname: `/event/${idx}`,
                      search: `?cate=${event.category}`,
                    }}
                    className="item"
                    state={{ event }}
                  >
                    <div className="thumbnail">
                      <img src={event.img} alt={event.title} />
                    </div>
                    <div className="overlay">
                      <div className="subject">
                        <div className="tit">{event.title}</div>
                        <div className="sub_tit">{event.title2}</div>
                      </div>
                      <div className="date">
                        {event.startDate} ~ {event.endDate}
                      </div>
                    </div>
                  </Link>
                </li>
              );
            })
          ) : (
            <li className="empty">   .</li>
          )}
        </ul>

        <div className="pagination">
          <button
            className="prev"
            onClick={() => handlePageChange(currentPage - 1)}
            disabled={currentPage === 1}
          >
            &laquo;
          </button>
          {eventPage.length !== 0 ? (
            eventPage.map((page) => (
              <button
                key={page}
                className={`page ${currentPage === page ? "active" : ""}`}
                onClick={() => handlePageChange(page)}
              >
                {page}
              </button>
            ))
          ) : (
            <button className="active">1</button>
          )}

          <button
            className="next"
            onClick={() => handlePageChange(currentPage + 1)}
            disabled={displayedEvents.length <= currentPage * eventitemsPerPage}
          >
            &raquo;
          </button>
        </div>
        {console.log(
          "currentPage",
          currentPage,
          "totalFilteredPages",
          totalFilteredPages
        )}
      </div>
    </div>
  );
};

export default Event;

// export default Event;
