import React from 'react';
import '../../App.css';
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer>
      <div className="menu_corp">
        <div className="layout_fix">
          <Link to="/"></Link>
          <Link to="/"></Link>
          <Link to="/"></Link>
          <Link to="/"></Link>
        </div>
      </div>
      <div className="fnb">
        <div className="layout_fix">
          <div className="logo_sns">
            <h1 className="starbucks_logo"><Link to="/" onClick={e=>e.preventDefault()}> </Link></h1>
            <div className="sns">
              <Link to="https://www.instagram.com/starbuckskorea" className="insta" title=""></Link>
              <Link to="https://www.youtube.com/user/starbuckskorea" className="youtube" title=""></Link>
              <Link to="https://x.com/StarbucksKorea" className="x" title=""></Link>
              <Link to="https://www.facebook.com/starbuckskorea" className="facebook" title=""></Link>
              {/* <Link to="/" className="blog" title=" "></Link> */}
            </div>
          </div>
          <div className="biz">
            <span> : Starbucks Coffee Korea Co., Ltd.</span>
            <span> :    100, 8~10(2)</span>
            <span> : </span>
            <span> : 201-81-21515 <button>[]</button></span>
            <span> : 1522-3232</span>
            <span>  : </span>
          </div>
          <div className="copyright">&copy; 2025 Starbucks Coffee Company. All Rights Reserved.</div>
          <div className="link">
            <div>
              <button><b></b></button>
              <button> </button>
              <button> </button>
              <button> </button>
            </div>
            <div>
              <button> </button>
              <button></button>
              <button></button>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;