import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useAccessToken } from "./AccessTokenContext.js";
import axios from "axios";
import { API_URL } from "config/constants.js";


const Login = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const { setAccessToken } = useAccessToken();
  const [userId, setUserId] = useState("");
  const [pw, setPw] = useState("");

  const onSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      const result = await axios.post(`${API_URL}/users/login`, {
        user_id: userId,
        pw: pw,
      });

      if (result.data.user === userId && result.data.accessToken) {
        alert(" .");
        // accessToken Context localStorage 
        setAccessToken(result.data.accessToken);
        localStorage.setItem('accessToken', result.data.accessToken);

        navigate('/'); //   
      } else {
        alert("   ");
      }
    } catch (err) {
      console.error("Login failed", err);
      alert("   .    .");
    } finally {
      setLoading(false);
    }
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <div id="container" className="member__Login">
      <div className="layout_fix">
          <div className="heading">
            <h2 className="tit"></h2>
            {/* <h5 className="desc">  .</h5> */}
          </div>
          <form onSubmit={onSubmit} onFinishFailed={onFinishFailed}>
            <div className="login_input">
              <input id="user_id" name="user_id" placeholder=" ." type="text" value={userId} onChange={(e)=>setUserId(e.target.value)} required/>
              <h5> </h5>
            </div>
            <div className="login_input">
              <input id="pw" name="pw" placeholder=" ." type="password" value={pw} onChange={(e)=>setPw(e.target.value)} required/>
              <h5></h5>
            </div>
            <div className="page_btn_wrap">
              <button className="btn_dark wid40" type="submit" disabled={!userId || !pw}></button>
            </div>
          </form>
      </div>
    </div>
  );
};

export default Login;