import React, {useEffect} from "react";
import { Link, Outlet, useNavigate } from "react-router-dom";
import { useUtilContext } from "hooks/UtilContext";
import './Store.css';

const Store = () => {
  const {isActive, toggleActive, selectedCategory, handleCategoryClick} = useUtilContext();
  const navigate = useNavigate();
  useEffect(() => {
        navigate("/store", { replace: true });
    }, []);

  return (
    <div id="container" className="board__store">
      <div className="layout_fix">
        <div className="heading">
          <h2 className="tit"></h2>
          <ul className="sort_list">
            <li className="sort_item">
              <label></label>
              <Link 
                className={`${isActive ? "active" : ""}`}
                onClick={toggleActive}
                data-discover="true"
              >{selectedCategory}</Link>
              <ul className="dropdown">
                <li><Link to="/store/map" data-discover="true" onClick={() => handleCategoryClick(" ")}> </Link></li>
                <li><Link to="/store/drive" data-discover="true" onClick={() => handleCategoryClick(" ")}> </Link></li>
                <li><Link to="/store/reserve" data-discover="true" onClick={() => handleCategoryClick(" ")}> </Link></li>
              </ul>
            </li>
          </ul>
        </div>
        <Outlet />
      </div>
    </div>
  );
};

export default Store;
