import { useEffect, useState } from "react";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import axios from "axios";

  /* ===
    categoryLabel : 
    category : 
    data :  
    categoryData :   
    matchingCategory :   
    foundProduct :   (setProductMatch)

    productMatch :  (productMatch)
    title : 
    cateKo : 
    cateList :   
  === */ 

const useProductMatch = () => {
  const navigate = useNavigate();
  const { category } = useParams(); // URL category 
  const [ searchParams ] = useSearchParams(); // URL   

  const [productMatch, setProductMatch] = useState(null); //    
  const [title,setTitle] = useState(""); //
  const [cateKo,setCateKo] = useState(""); //
  const [cateList, setCateList] = useState([]); //( )

  const cateIndex = searchParams.get("cate"); // cate 
  const productId = searchParams.get("id"); // id 

  useEffect(() => {
    const fetchData = () => {
      axios
        .get("https://raw.githubusercontent.com/deliondane/db/main/db.json")
        .then((res) => {
          const data = res.data; //    
          console.log("==", data);
          let categoryData = []; // category   
          let categoryLabel = ""; //  
          switch (category) {
            case "coffee":
              categoryData = data.coffee;
              categoryLabel = "";
              break;
            case "beverage":
              categoryData = data.beverage;
              categoryLabel = "";
              break;
            case "product":
              categoryData = data.product;
              categoryLabel = "";
              break;
            case "food":
              categoryData = data.food;
              categoryLabel = "";
              break;
            case "promotion":
              categoryData = data.promotion;
              categoryLabel = "";
              break;
            default:
              console.log("err==", category); //   
              return;
          }
          setTitle(categoryLabel);
          console.log(`${categoryLabel} ${category}`);
          console.log("==", categoryData);

          
          // cate, id   
          const matchingCategory = categoryData[cateIndex];
          console.log("==", matchingCategory);
          setCateKo(matchingCategory.category);
          setCateList(matchingCategory.products);

          if (matchingCategory) {
            const foundProduct = matchingCategory.products.find(
              (item) => item.id.toString() === productId
            );
            // console.log("==", foundProduct);
            setProductMatch(foundProduct);
          }

        })
        .catch((err) => {
          console.error("Error fetching data:", err);
        });
    };

    fetchData();
  }, [category, cateIndex, productId]);


  useEffect(()=>{
    const recommended = document.querySelector(".detail_recommended");
    if(recommended){
      setTimeout(() => {
        recommended.querySelectorAll(".prd_list .name").forEach(nameEl => {
          const prdName = nameEl.textContent;
          const nameLink = nameEl.closest(".item").querySelector("a");

          const recommendedItem = cateList.map(items => ({
            categoryEn: cateIndex,
            cateNum: items.id,
            ...items
          })).find(item => item.name === prdName);
          
          // console.log(nameEl, nameEl.closest(".item").querySelector("a"));
          nameLink.setAttribute("href", `/menu/detail/${category}?cate=${recommendedItem.categoryEn}&id=${recommendedItem.cateNum}`);
          nameLink.addEventListener("click",function(e){
            e.preventDefault();
            navigate(`/menu/detail/${category}?cate=${recommendedItem.categoryEn}&id=${recommendedItem.cateNum}`);
          })

        })
      }, 500);
    }
  },[cateList])

  // console.log("==", productMatch);
  
  // prev,next 
  if(!productMatch){
    return { prev:null, next:null };
  }
  const itemIdx = cateList.findIndex((el)=> el.id === productMatch.id);
  const itemPrev = cateList[itemIdx - 1] !== undefined ? cateList[itemIdx - 1] : null;
  const itemNext = cateList[itemIdx + 1] !== undefined ? cateList[itemIdx + 1] : null;


  return { productMatch, title, cateKo, cateList, itemPrev, itemNext };
};

export default useProductMatch;