import React, { useEffect, useState } from "react";
import { Link, useNavigate, useLocation } from "react-router-dom";
import { useUtilContext } from "hooks/UtilContext";

import "pages/Event/Customer.css";

const Notice = () => {
  useEffect(() => {
    setSearch(""); //     
  }, []); //         

  const {
    isActive,
    selectedCategory,
    currentPage,
    setCurrentPage,
    toggleActive,
    handleCategoryClick,
    handlePageChange,
    search,
    setSearch,
    maxVisiblePages,
    currentBlock,
    startPage,
    noticePages,
    noticeitemsPerPage,
    noticetotalPages,
    datefilteredNotice,
    paginatedNotices,
  } = useUtilContext();

  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (location.search.includes("cate=")) {
      navigate("/notice", { replace: true });
    }
  }, []);

  const handleCategoryChange = (e) => {
    handleCategoryClick(selectedCategory);
    navigate(`/notice?cate=${e}`);
  };
  const [filteredNotices, setFilteredNotices] = useState(datefilteredNotice);
  const searchAction = (e) => {
    e.target.closest(".search_notice").classList.toggle("active");
  };

  useEffect(() => {
    if (search.trim === "") {
      setFilteredNotices(datefilteredNotice);
    } else {
      const filtered = datefilteredNotice.filter((notice) =>
        notice.subject.toLowerCase().includes(search.toLowerCase())
      );
      setFilteredNotices(filtered);
    }
    setCurrentPage(1);
  }, [search, selectedCategory, datefilteredNotice, setCurrentPage]);

  //    
  const isSearching = search.trim() !== ""; // 
  const displayedNotice = isSearching ? filteredNotices : paginatedNotices; //    
  const noticeSearchTotalPages = Math.ceil(
    filteredNotices.length / noticeitemsPerPage
  );
  const noticeSearchEndPage = Math.min(
    currentBlock * maxVisiblePages,
    noticeSearchTotalPages
  );
  const searchPages = Array.from(
    { length: noticeSearchEndPage - startPage + 1 },
    (_, i) => startPage + i
  );
  const noticePage = isSearching ? searchPages : noticePages;

  return (
    <>
      <div id="container" className="board__notice">
        <div className="layout_fix">
          <div className="heading">
            <h2 className="tit">
              <Link to="/notice?cate"></Link>
            </h2>
            <ul className="sort_list">
              <li className={`sort_item`}>
                <label></label>
                <Link
                  className={`${isActive ? "active" : ""}`}
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    toggleActive();
                  }}
                >
                  {selectedCategory}
                </Link>
                <ul className="dropdown">
                  {["", "", "", ""].map(
                    (category) => (
                      <li key={category}>
                        <Link
                          to={`/notice?cate=${category}`}
                          onClick={(e) => {
                            e.preventDefault();
                            e.stopPropagation();
                            handleCategoryChange(category);
                            handleCategoryClick(category);
                          }}
                        >
                          {category}
                        </Link>
                      </li>
                    )
                  )}
                </ul>
              </li>
            </ul>
            <div className="search_notice">
              <input
                value={search}
                onChange={(e) => {
                  setSearch(e.target.value);
                }}
                type="text"
                placeholder=" "
              />
              <button className="btn_search" onClick={(e) => searchAction(e)}>
                Search
              </button>
            </div>
          </div>

          {displayedNotice.length > 0 ? (
            <table className="tb_list">
              <colgroup>
                <col style={{ width: "100px" }} />
                <col style={{ width: "160px" }} />
                <col style={{ width: "auto" }} />
                <col style={{ width: "120px" }} />
                <col style={{ width: "120px" }} />
              </colgroup>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {displayedNotice.map((notice, key) => (
                  <tr key={key}>
                    <td>{(currentPage - 1) * noticeitemsPerPage + key + 1}</td>
                    <td>{notice.category}</td>
                    <td className="subject">
                      <Link
                        to={{
                          pathname: `/notice/${notice.key}`,
                          search: `?cate=${notice.category}`,
                        }}
                      >
                        {notice.subject}
                      </Link>
                    </td>
                    <td></td>
                    <td>{notice.date}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <>
              <table className="tb_list">
                <colgroup>
                  <col style={{ width: "100px" }} />
                  <col style={{ width: "160px" }} />
                  <col style={{ width: "auto" }} />
                  <col style={{ width: "120px" }} />
                  <col style={{ width: "120px" }} />
                </colgroup>
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
              </table>
              <div className="empty">   .</div>
            </>
          )}
          <div className="pagination">
            <button
              className="prev"
              onClick={() => handlePageChange(currentPage - 1)}
              disabled={currentPage === 1}
            >
              &laquo;
            </button>
            {noticePage.length !== 0 ? (
              noticePage.map((page) => (
                <button
                  key={page}
                  className={`page ${currentPage === page ? "active" : ""}`}
                  onClick={() => handlePageChange(page)}
                >
                  {page}
                </button>
              ))
            ) : (
              <button className="active">1</button>
            )}
            <button
              className="next"
              onClick={() => handlePageChange(currentPage + 1)}
              disabled={currentPage === noticetotalPages}
            >
              &raquo;
            </button>
          </div>
          {/* { console.log("currentPage",currentPage,"noticetotalPages",noticetotalPages) } */}
        </div>
      </div>
    </>
  );
};

export default Notice;
